.layout {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url(/HotelsChecker/static/media/login.49c99287.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 409px;
  padding: 32px;
  border-radius: 16px;
  background-color: #ffffff;
  box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
}

.login__title {
  margin-bottom: 8px;
  font-size: 24px;
  font-weight: 500;
  line-height: 28px;
  color: #424242;
  text-align: center;
}

.login__formfield {
  position: relative;
  margin-top: 24px;
}

.login__input-title {
  display: block;
  margin-bottom: 7px;
  font-size: 16px;
  font-weight: 300;
  line-height: 19px;
  color: #424242;
}

.login__input {
  width: 100%;
  padding: 16px;
  border: 1px solid #c9cacc;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 300;
  color: #424242;
}

.login__formfield--error .login__input {
  border-color: #eb1717;
}

.login__formfield--error .login__input-title,
.login__formfield--error .login__input,
.login__message {
  color: #eb1717;
}

.login__message {
  position: absolute;
  display: none;
  margin-top: 4px;
  font-size: 12px;
  font-weight: 300;
  line-height: 14px;
}

.login__formfield--error .login__message {
  display: block;
}

.login__btn {
  padding: 15px;
  margin-top: 32px;
  border: 1.5px solid #41522e;
  border-radius: 4px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
  background: linear-gradient(104.34deg, #41522e -15.34%, #be8022 145.95%);
  font-family: inherit;
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;
}

.login__btn:hover,
.login__btn:active {
  cursor: pointer;
}

