  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 32px;
  margin-bottom: 24px;
  border-radius: 16px;
  background-color: #ffffff;
  box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
