  height: 64px;
  margin-right: 24px;
  border-radius: 50px;
  background-color: rgba(65, 82, 46, 0.05);
  background-image: url(/HotelsChecker/static/media/logo.2685f6fc.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 35px;
}

.hotel__wrapper {
  flex: 1 1 auto;
}

.hotel__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hotel__title {
  font-size: 17px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: -0.408px;
  color: #424242;
}

.hotel__favorite-btn {
  width: 21px;
  height: 18px;
  border: none;
  background-color: transparent;
  background-image: url(/HotelsChecker/static/media/fav.595df94b.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 21px 18px;
}

.hotel__favorite-btn--active {
  background-image: url(/HotelsChecker/static/media/fav-active.3d60a0c0.svg);
}

.hotel__info {
  margin: 4px 0;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.408px;
  color: #878787;
}

.hotel__info-separator {
  margin: 0 14px;
}

.hotel__footer {
  display: flex;
  justify-content: space-between;
}

.hotel__rating {
  display: flex;
  align-self: flex-end;
}

.rating-icon {
  margin-right: 3px;
  fill: #cdbc1e;
}

.hotel__price {
  font-size: 11px;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: -0.408px;
  color: #878787;
}

.hotel__price-value {
