  font-size: 17px;
  line-height: 22px;
  letter-spacing: -0.408px;
  color: #424242;
}

.favorites {
  padding: 32px;
  border-radius: 16px;
  background-color: #ffffff;
  box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
}

.favorites__title {
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 500;
  line-height: 28px;
  color: #424242;
}

.favorites__sorting-list {
  display: flex;
  margin-bottom: 16px;
}

.favorites__sorting-item {
  display: flex;
  align-items: center;
  padding: 4px 8px;
  margin-right: 8px;
  border: 1px solid #e5e5e5;
